* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html, body {
  background: linear-gradient(90deg, #69b7eb, #b3dbd3, #f4d6db);
  font-size: 1.1rem;
  font-family: "Quicksand", sans-serif;
  height: 100%;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.Todo {
    display: flex;
    margin: 0 -3rem 4px;
    padding: 1.1rem 3rem;
    justify-content: space-between ;
    align-items: center;
    background: rgba(255,255,255, 0.1);
}

.Todo-task.completed {
    position: relative;
    opacity: 0.6;
}

.Todo-task.completed:before{
    content: "";
    position: absolute;
    top: 50%;
    left: -0.5rem; /* starting line through a bit before text */
    display: block; 
    width: 0%;
    height: 2px;
    background: #fff;
    -webkit-animation: strikeitem 0.5s ease-out 0s forwards;
            animation: strikeitem 0.5s ease-out 0s forwards;
    /* animates line from left to right */
}

@-webkit-keyframes strikeitem {
    to {
        width: calc(100% + 1rem); /* line goes through text & a little after it*/
    }
}

@keyframes strikeitem {
    to {
        width: calc(100% + 1rem); /* line goes through text & a little after it*/
    }
}

.Todo-buttons {
    flex-shrink: 0;
    padding-left: 0.7em;
}

.Todo-buttons button{
    border:  none;
    font-size: 1em;
    margin: .4em;
    background: none;
    cursor: pointer;
    color: #fff;
}

.Todo-edit-form{
    display: flex;
    flex-wrap: wrap;
}

.Todo-edit-form label{
    min-width: 100%;
    margin-bottom: 0.5rem;
    font-size: 1.3rem;
}

.Todo-edit-form input{
    flex-grow: 1;
    border: none;
    background: #f7f1f1;
    padding: 0 1.5em;
    font-size: medium;
    font-size: initial;
}

.Todo-edit-form button{
    padding: 0 1.3rem;
    border: none;
    background: #ff6666;
    color: white;
    text-transform: uppercase;
    font-weight: bold;
    border: 1px solid rgba(255,255,255, 0.3);
    margin-left: 5px;
    cursor: pointer;
    transition: background 0.2s ease-out;
}

.Todo-edit-form button:hover{
    background: #ff5e5e;
}

.Todo-edit-form input, 
.Todo-edit-form button {
    font-family: "Quicksand, sans-serif";
    height: 3rem;
}
.NewTodoForm {
    margin-top: 3rem;
    display: flex;
    flex-wrap: wrap;
}

.NewTodoForm label{
    min-width: 100%;
    margin-bottom: 0.5rem;
    font-size: 1.3rem;
}

.NewTodoForm input{
    flex-grow: 1;
    border: none;
    background: #f7f1f1;
    padding: 0 1.5em;
    font-size: medium;
    font-size: initial;
}

.NewTodoForm button{
    padding: 0 1.3rem;
    border: none;
    background: #ff6666;
    color: white;
    text-transform: uppercase;
    font-weight: bold;
    border: 1px solid rgba(255,255,255, 0.3);
    margin-left: 5px;
    cursor: pointer;
    transition: background 0.2s ease-out;
}

.NewTodoForm button:hover{
    background: #ff5e5e;
}

.NewTodoForm input, 
.NewTodoForm button {
    font-family: "Quicksand, sans-serif";
    height: 3rem;
}
.TodoList {
    margin: 4rem auto;
    padding: 4rem 3rem 3rem;
    max-width: 500px;
    background: #ff6666;
    color: #fff;
    box-shadow: -20px -20px 0px 0px rgba(100, 100, 100, 0.1);
}

.TodoList ul {
    margin-top: 2.6rem;
    list-style: none;
}

.TodoList h1 {
    font-weight: normal;
    font-size: 2.6rem;
    letter-spacing: 0.05em;
    border-bottom: 1px solid rgba(255, 255, 255, 0.3);
}

.TodoList h1 span {
    display: block;
    font-size: 0.8rem;
    margin-bottom: 0.7rem;
    margin-left: 3px;
    margin-top: 0.2rem;
}
